import React, { useState } from "react";
import { makeStyles, Paper, Grid, Typography } from "@material-ui/core";
import { datalist } from "./data";

const useStyles = makeStyles((theme) => ({
  space: {
    margin: theme.spacing(1),
    width: theme.spacing(100),
    height: theme.spacing(25),
    cursor: "pointer",
    marginLeft: "auto",
    marginRight: "auto",
  },
  text: {
    paddingTop: theme.spacing(10),
    fontSize: "2rem",
  },
  top: {
    marginTop: theme.spacing(10),
  },
  pad: {
    padding: theme.spacing(5),
  },
  read: {
    paddingTop: theme.spacing(5),
    paddingLeft: theme.spacing(5),
    paddingRight: theme.spacing(5),
  },
  logo: {
    width: "100px",
    height: "100px",
  },
}));

export default function Projects() {
  const classes = useStyles();
  const [photographie, setPhotographie] = useState("");
  const [selected, setSelected] = useState("");

  const hoverPicture = () => {
    photographie === "" ? setPhotographie("") : setPhotographie(photographie);
  };

  const handleVisible = (id) => {
    setSelected(id);
  };

  return (
    <Grid className={classes.top} container justify="center" direction="column">
      {datalist.map((data) => (
        <Grid item key={data.id}>
          <Paper
            elevation={3}
            className={classes.space}
            style={{
              backgroundImage: data.id === selected ? "" : `url(${data.logo})`,
            //   backgroundSize: "100px 100px",
              backgroundSize: "cover",
              backgroundRepeat: "no-repeat",
            //   backgroundColor: "rgba(0,0,0,0.3)",
            // filter: "blur(8px)"
            }}
            onMouseEnter={() => handleVisible(data.id)}
            onMouseLeave={() => setSelected("")}
          >
            {data.id === selected ? (
              <>
                <Typography align="justify" className={classes.read}>
                  {data.description}
                </Typography>
                <Typography align="justify" className={classes.read}>
                  Stack : {data.techno}
                </Typography>
              </>
            ) : (
              <>
                <Typography align="center" className={classes.text}>
                  {data.name}
                </Typography>
                <Grid
                  container
                  spacing={3}
                  className={classes.pad}
                  justify="flex-end"
                >
                  <Grid item>
                    <Typography>{data.type}</Typography>
                  </Grid>
                  <Grid item>
                    <Typography>{data.date}</Typography>
                  </Grid>
                </Grid>
              </>
            )}
          </Paper>
          <Paper>
            {/* <img className={classes.logo} src={data.logo} /> */}
          </Paper>
        </Grid>
      ))}
    </Grid>
  );
}
