import React, { useState } from "react";
import { makeStyles, Paper, Grid, Typography } from "@material-ui/core";

const useStyles = makeStyles((theme) => ({
  root: {
    display: "flex",
    flexWrap: "wrap",
    "& > *": {
      margin: theme.spacing(1),
      width: theme.spacing(16),
      height: theme.spacing(16),
    },
    marginTop: theme.spacing(10),
  },
  space: {
    margin: theme.spacing(1),
    width: theme.spacing(25),
    height: theme.spacing(25),
    cursor: "pointer",
    backgroundColor: "#A5A5A5",
  },
  hoverspace: {
    margin: theme.spacing(1),
    width: theme.spacing(25),
    height: theme.spacing(25),
    cursor: "pointer",
    // backgroundColor: "#A5A5A5"
  },
  text: {
    paddingTop: theme.spacing(10),
    color: "#fff",
    fontWeight: "bold",
  },
  hovertext: {
    paddingTop: theme.spacing(10),
    // color: "#fff",
    // fontWeight: "bold"
  },
  top: {
    marginTop: theme.spacing(10),
  },
}));

export default function Navigator() {
  const classes = useStyles();
  const [hovered, setHovered] = useState("");
  const handleHover = (id) => setHovered(id);

  return (
    <Grid className={classes.top} container justify="center">
      <Grid
        item
        onMouseEnter={() => handleHover(1)}
        onMouseLeave={() => setHovered("")}
      >
        <Paper
          elevation={3}
          className={hovered === 1 ? classes.hoverspace : classes.space}
        >
          <Typography
            align="center"
            className={hovered === 1 ? classes.hovertext : classes.text}
          >
            Projects
          </Typography>
        </Paper>
      </Grid>
      <Grid
        item
        onMouseEnter={() => handleHover(2)}
        onMouseLeave={() => setHovered("")}
      >
        <Paper
          elevation={3}
          className={hovered === 2 ? classes.hoverspace : classes.space}
        >
          <Typography
            align="center"
            className={hovered === 2 ? classes.hovertext : classes.text}
          >
            Resume
          </Typography>
        </Paper>
      </Grid>
      <Grid
        item
        onMouseEnter={() => handleHover(3)}
        onMouseLeave={() => setHovered("")}
      >
        <Paper
          elevation={3}
          className={hovered === 3 ? classes.hoverspace : classes.space}
        >
          <Typography
            align="center"
            className={hovered === 3 ? classes.hovertext : classes.text}
          >
            Contact
          </Typography>
        </Paper>
      </Grid>
    </Grid>
  );
}
